.scoreboard{
    padding: 2rem;
}
.image-star{
    background-color: hsl(216, 11%, 36%);
    text-align: center;
    border-radius: 100% ;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
}
.star{
    width: 40%;
    height: 40%;
    margin-top: 0.8rem;
}
h1{
    margin-top: 2rem;
    color: hsl(0, 0%, 100%);
}
p{
    color: hsl(217, 12%, 63%);
    margin-top: 0.7rem;
}
.score-button{
    display: flex;
    margin-top: 1rem;
    justify-content: space-between;

}
.score-button button{
    color: hsl(217, 12%, 63%);
    border-radius: 100%;
    border: none;
    background-color: hsl(216, 11%, 36%);;
    width: 50px;
    height: 50px;
    cursor: pointer;
}
.score-button button:hover{
    transition: 200ms;
    color: hsl(0, 0%, 100%);
    background-color: hsl(25, 97%, 53%);
}
.score-button button:active{
    transition:none;
    background-color:hsl(217, 12%, 63%);
    color:hsl(0, 0%, 100%);
}
.submit-button{
    cursor: pointer;
    background-color:hsl(25, 97%, 53%);
    border: none;
    border-radius: 50px;
    height: 50px;
    width: 100%;
    margin-top: 3rem;
}
.submit-button:hover{
    transition: 200ms;
    background-color: hsl(0, 0%, 100%);
    color: hsl(25, 97%, 53%);
}